.bento {
	background-color: $base-100;
	padding: 10px;
	border-radius: 30px;
	.bento-grid {
		display: grid;
		gap: 30px;
		grid-template-columns: repeat(5, 1fr);
		grid-template-rows: repeat(4, 1fr);
		height: 960px;
		@media(max-width:1024px) {
			grid-template-rows: repeat(8,1fr);
		}
		@media(max-width:768px) {
			display: flex;
			flex-direction: column;
			gap: 30px;
			height: auto;
		}
		.bento-items {
			padding: 30px;
			background-color: $base-100;
			border-radius: 15px;
			position: relative;
			overflow: hidden;
			img {
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				object-fit: cover;
				transition: ease 0.3s all;
				&:hover {
						transform: scale(1.2);
				
				}
			}
			@media(max-width:768px) {
				height: 240px;
			}
			&:nth-child(1) {
				grid-column: span 2;
				grid-row: span 2;
				@media(max-width:1024px) {
					grid-column: span 2;
					grid-row: span 2;
				}
			}
			&:nth-child(2) {
				grid-column: span 2;
				grid-row: span 1;
				@media(max-width:1024px) {
					grid-column: span 3;
					grid-row: span 2;
				}
			}
			&:nth-child(3) {
				grid-column: span 1;
				grid-row: span 1;
				@media(max-width:1024px) {
					grid-column: span 3;
					grid-row: span 2;
				}
			}
			&:nth-child(4) {
				grid-column: span 1;
				grid-row: span 1;
				@media(max-width:1024px) {
					grid-column: span 2;
					grid-row: span 4;
				}
			}
			&:nth-child(5) {
				grid-column: span 2;
				grid-row: span 1;
				@media(max-width:1024px) {
					grid-column: span 3;
					grid-row: span 2;
				}
			}
			&:nth-child(6) {
				grid-column: span 5;
				grid-row: span 3;
				@media(max-width:1024px) {
					grid-column: span 5;
					grid-row: span 2;
				}
			}
		}
	}
}