.chatbot {
	.chatbot-blue {
		background-color: $blue-100;
		border-radius: 30px;
		padding: 30px;
		gap: 30px;
		display: flex;
		@media(max-width:1024px) {
			flex-direction: column;
		}
		p {
			margin-bottom: 30px;
		}
		.chat-info {
			flex: 3;
			padding-right: 30px;
		}
		.chat-box {
			flex: 4;
			background-color: #fff;
			border-radius: 15px;
			height: 500px;
			padding: 20px;
			display: flex;
			flex-direction: column;
			.scroll-area {
				max-height: 300px;
				height: 300px;
				padding: 20px;
				overflow-y: scroll;
				ul {
					display: flex;
					flex-direction: column;
					justify-content: flex-end;
					gap: 15px;
					li {
						display: flex;
						align-items: center;
						gap: 15px;
						&:nth-child(even) {
							text-align: right;
							justify-content: flex-end;
							flex-direction: row-reverse;
							span {
								background-color: $yellow;
							}
						}
						span {
							background-color: $blue-400;
							width: 64px;
							height: 64px;
							border-radius: 50%;
							display: inline-flex;
							align-items: center;
							justify-content: center;
							@media (max-width:768px) {
								width: 48px;
								height: 48px;
							}
						}
						div {
							flex: 1;
						}
					}
				}
			}
			.chat-message {
				display: flex;
				margin-top: 15px;
				gap: 15px;
				input {
					border-radius: 10px;
					flex: 2;
					padding: 15px;
					border: 1px solid transparent;
					background-color: $base-100;
					&:hover {
						outline: none;
						background-color: #fff;
						border: 1px solid $base-100;
					}
					&:focus {
						outline: none;
						background-color: #fff;
						border: 1px solid $base-100;
					}
				}
				button {
					border: none;
					cursor: pointer;
				}
			}
		}
	}

}